export default {
    computed: {
        battleResults() {
            return this.$store.getters['battle/getBattleResults']
        },
        battleSize() {
            return this.$store.getters['battle/getBattleSize']
        },
        customizedBattleResults() {
            return this.$store.getters['battle/getCustomizedBattleResults']
        },
        selectedAllyPokemon() {
            return this.$store.getters['battle/getSelectedAllyPokemon']
        }
    },
    methods: {
        setBattleResults(weak, pokemon, tier, defense) {
            this.$store.commit('battle/SET_BATTLE_RESULTS', {
                weak: weak,
                pokemon: pokemon,
                tier: tier, 
                defense: (false || defense)
            })
        },
        setSelectedAllyPokemon(pokemon) {
            this.$store.commit('battle/SET_SELECTED_ALLY_POKEMON', pokemon)
        }
    }
}